<template>
  <div class="demo-page svg-demo">
    <el-row>

      <el-col :span="24">
        <div class="svg__wrapper">
          <svg xmlns="http://www.w3.org/2000/svg" version="1.1" class="show-border">
            <rect
                    :x="rectConfig.x"
                    :y="rectConfig.y"
                    :width="rectConfig.width"
                    :height="rectConfig.height"
                    :rx="rectConfig.rx"
                    :ry="rectConfig.ry"
                    :stroke="rectConfig.stroke"
                    :stroke-width="rectConfig.strokeWidth"
                    :fill="rectConfig.fill" ></rect>
          </svg>
        </div>
        <div class="svg-config-form__wrapper">
          <el-form ref="circleConfigForm" :model="rectConfig" label-width="200px">
            <el-form-item label="位置(左)(x)">
              <el-slider
                  v-model="rectConfig.x"
                  :min="50"
                  :max="300"
                  :step="50"
                  show-stops>
              </el-slider>
            </el-form-item>

            <el-form-item label="位置(右)(y)">
              <el-slider
                  v-model="rectConfig.y"
                  :min="50"
                  :max="300"
                  :step="50"
                  show-stops>
              </el-slider>
            </el-form-item>

            <el-form-item label="宽度(width)">
              <el-slider
                  v-model="rectConfig.width"
                  :min="50"
                  :max="300"
                  :step="50"
                  show-stops>
              </el-slider>
            </el-form-item>

            <el-form-item label="高度(height)">
              <el-slider
                  v-model="rectConfig.height"
                  :min="50"
                  :max="300"
                  :step="50"
                  show-stops>
              </el-slider>
            </el-form-item>

            <el-form-item label="圆角(水平)(rx)">
              <el-slider
                  v-model="rectConfig.rx"
                  :max="100"
                  :step="10"
                  show-stops>
              </el-slider>
            </el-form-item>

            <el-form-item label="圆角(垂直)(ry)">
              <el-slider
                  v-model="rectConfig.ry"
                  :max="100"
                  :step="10"
                  show-stops>
              </el-slider>
            </el-form-item>

            <el-form-item label="笔划颜色(stroke)">
              <el-color-picker v-model="rectConfig.stroke" show-alpha >
              </el-color-picker>
              {{rectConfig.stroke}}
            </el-form-item>

            <el-form-item label="笔划宽度(strokeWidth)">
              <el-slider
                  v-model="rectConfig.strokeWidth"
                  :max="20"
                  :step="1"
                  show-stops>
              </el-slider>
            </el-form-item>

            <el-form-item label="填充颜色(fill)">
              <el-color-picker v-model="rectConfig.fill" show-alpha >
              </el-color-picker>
              {{rectConfig.fill}}
            </el-form-item>

          </el-form>
        </div>
      </el-col>
    </el-row>

  </div>
</template>

<script>
export default {
  name: 'SVGDemo001',
  data () {
    return {
      rectConfig: {
        x: 100,
        y: 50,
        width: 100,
        height: 50,
        rx: 40,
        ry: 40,
        stroke: 'rgba(0, 0, 0, 0.8)',
        strokeWidth: 2,
        fill: 'rgba(255, 69, 0, 0.68)'
      }
    }
  }
}
</script>

<style scoped>

</style>